body {
  height: 100vh !important;
  overflow: hidden !important;
}
#root {
  height: 100%;
}
/*outer design*/
#chat_window {
  height: 90%;
  width: 90%;
  margin-right: auto;
  margin-left: auto;
}

#chat_card {
  border-radius: 10px 0 0 0;
  border: 2px solid #adb5bd;
  padding-right: 0;
  padding-left: 0;
  background-color: #f6f5f5;
  height: 100%;
}

#panels_row {
  height: 100%;
}

.chat-panel {
  padding-right: 0px;
  padding-left: 0px;
  height: 100%;
}
.chat-panel > * {
  margin: 0;
}

/*conversations panel*/
#conversations_panel {
  border-right: 2px solid #adb5bd;
}
#conversation_panel .date {
  font-size: 10px;
  text-align: center;

}

#user_info {
  border-radius: 10px 0 0 0;
  background-color: rgb(215, 224, 230);
}

#conversations {
  height: 90%;
  overflow-x: hidden;
  overflow-y: auto;
}

.chat-row {
  height: 10%;
  margin: 0;
}
.active_chat {
  background-color: #d3d3d3;
}
.chat-row .last_msg {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
}

.contact_name {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
}

.name-time-last-message-container {
  align-content: space-around;
  overflow: hidden;
  width:100%;
}

/*messages panel*/

#conversation_info_row {
  background-color: rgb(215, 224, 230);
}

#message_box {
  flex-direction: column;
  height: 85%;
  background-image: url(/public/pictures/MessagesBackground.jpg);
  background-size: contain;
  overflow-y: auto;
  overflow-x: hidden;
}

#chat_input {
  height: 5%;
}
#input_message {
  display: flex;
}
.messages-buffer {
  min-height: 12px;
  flex: 1 1 auto;
  width: auto;
  padding: 0;
}
.messages {
  display: block;
}
.message {
  margin-bottom: 20px;
  padding: 0 50px 0 50px;
  display: flex;
  flex-direction: column;
  line-height: 1.5;
}
.message > [class^="message-"] {
  padding: 10px;
}
.message-in {
  background-color: white;
  align-self: flex-start;
  border-radius: 10px 10px 10px 0;
}
.message-in > .message-time {
  text-align: end;
}
.message-out {
  background-color: rgb(217, 253, 211);
  align-self: flex-end;
  border-radius: 10px 10px 0 10px;
}
.message-text {
  display: block;
}
.message-time {
  display: block;
  font-size: 0.75em;
}

/*shared componenets*/
[class^="col-"].profile-container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.profile-container img {
  border-radius: 50%;
  object-fit: cover;
  /*xs size*/
  height: 25px;
  width: 25px;
}
/*small*/
@media (min-width: 576px){
	.profile-container img {
		height: 30px;
		width: 30px;
	}
}
/*medium*/
@media (min-width: 768px){
	.profile-container img {
		height: 35px;
		width: 35px;
	}
}
/*large*/
@media (min-width: 992px){
	.profile-container img {
		height: 40px;
		width: 40px;
	}
}
/*xl*/
@media (min-width: 1200px){
	.profile-container img {
		height: 45px;
		width: 45px;
	}
}
/*xxl*/
@media (min-width: 1400px){
	.profile-container img {
		height: 50px;
		width: 50px;
	}
}
.date{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}